import React from "react";
import Layout from "../layouts";
import { StaticImage } from "gatsby-plugin-image";
import { Helmet } from "react-helmet";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";

export default class ContactUs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      subject: "",
      content: "",
      loading: false,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleInputChange(event) {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  }
  handleSubmit(event) {
    event.preventDefault();
    this.setState({ loading: true });
    axios
      .put(
        "https://api.contentful.com/spaces/5jo55pzix7dl/environments/master/entries/" +
          uuidv4(),
        {
          fields: {
            name: {
              "en-US": this.state.name,
            },
            email: {
              "en-US": this.state.email,
            },
            subject: {
              "en-US": this.state.subject,
            },
            content: {
              "en-US": this.state.content,
            },
          },
        },
        {
          headers: {
            "X-Contentful-Content-Type": "contactForm",
            Authorization:
              "Bearer CFPAT--oO7XY1q3w8WNzgSelVpiud4Dpc6cly9oXHDlN26hrs",
          },
        }
      )
      .then((res) => {
        alert(
          "Thank you for getting in touch!  One of our colleagues will get back in touch with you soon. Have a great day!"
        );
        this.setState({
          name: "",
          email: "",
          subject: "",
          content: "",
          loading: false,
        });
      })
      .catch((err) => {
        console.log(err);
        this.setState({ loading: false });
      });
  }
  render() {
    return (
      <Layout>
        <Helmet>
          <title>Contact us</title>
        </Helmet>
        <div
          className="page-heading contact-heading header-text"
          //style="background-image: url(assets/images/heading-4-1920x500.jpg);"
        >
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="text-content">
                  <h4>Lorem ipsum dolor</h4>
                  <h2>Contact Us</h2>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="find-us">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="section-heading">
                  <h2>Our Location on Maps</h2>
                </div>
              </div>
              <div className="col-md-8">
                <div id="map">
                  <iframe
                    title="Map"
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3724.9406592077976!2d105.85963921528757!3d20.99501568601665!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3135ac0fd6efffff%3A0xcd0762df50adef3e!2sFFW!5e0!3m2!1sen!2s!4v1626862231828!5m2!1sen!2s"
                    width="100%"
                    height="450"
                    allowFullScreen=""
                    loading="lazy"
                    frameBorder="0"
                  ></iframe>
                </div>
              </div>
              <div className="col-md-4">
                <div className="left-content">
                  <h4>About our office</h4>
                  <p>
                    Lorem ipsum dolor sit amet, consectetur adipisic elit. Sed
                    voluptate nihil eumester consectetur similiqu consectetur.
                    <br />
                    <br />
                    Lorem ipsum dolor sit amet, consectetur adipisic elit. Et,
                    consequuntur, modi mollitia corporis ipsa voluptate
                    corrupti.
                  </p>
                  <ul className="social-icons">
                    <li>
                      <a href="https://facebook.com">
                        <i className="fa fa-facebook"></i>
                      </a>
                    </li>
                    <li>
                      <a href="https://facebook.com">
                        <i className="fa fa-twitter"></i>
                      </a>
                    </li>
                    <li>
                      <a href="https://facebook.com">
                        <i className="fa fa-linkedin"></i>
                      </a>
                    </li>
                    <li>
                      <a href="https://facebook.com">
                        <i className="fa fa-behance"></i>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="send-message">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="section-heading">
                  <h2>Send us a Message</h2>
                </div>
              </div>
              <div className="col-md-8">
                <div className="contact-form">
                  <form id="contact" action="" method="post">
                    <div className="row">
                      <div className="col-lg-12 col-md-12 col-sm-12">
                        <fieldset>
                          <input
                            name="name"
                            type="text"
                            className="form-control"
                            id="name"
                            value={this.state.name}
                            placeholder="Full Name"
                            onChange={this.handleInputChange}
                            required=""
                          />
                        </fieldset>
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12">
                        <fieldset>
                          <input
                            name="email"
                            type="text"
                            value={this.state.email}
                            className="form-control"
                            onChange={this.handleInputChange}
                            id="email"
                            placeholder="E-Mail Address"
                            required=""
                          />
                        </fieldset>
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12">
                        <fieldset>
                          <input
                            name="subject"
                            type="text"
                            value={this.state.subject}
                            className="form-control"
                            onChange={this.handleInputChange}
                            id="subject"
                            placeholder="Subject"
                            required=""
                          />
                        </fieldset>
                      </div>
                      <div className="col-lg-12">
                        <fieldset>
                          <textarea
                            name="content"
                            rows="6"
                            className="form-control"
                            id="content"
                            value={this.state.content}
                            placeholder="Your Message"
                            onChange={this.handleInputChange}
                            required=""
                          ></textarea>
                        </fieldset>
                      </div>
                      <div className="col-lg-12">
                        <fieldset>
                          <button
                            type="submit"
                            id="form-submit"
                            className="filled-button"
                            onClick={this.handleSubmit}
                            disabled={this.state.loading}
                          >
                            {this.state.loading ? "Loading" : "Send Message"}
                          </button>
                        </fieldset>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <div className="col-md-4">
                <StaticImage
                  className="img-fluid"
                  src="../assets/images/team_01.jpg"
                  alt="A dinosaur"
                />

                <h5 className="text-center">John Doe</h5>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}
